.App {
 
  display: flex;
  flex-direction: column;
  background:url("./images/bg.jpg") rgba(0, 0, 0, 0.957);
  background-blend-mode: color;
  background-size: cover;
  background-repeat: no-repeat;
  color: blanchedalmond;
 place-content: center;
 border: 0.1rem solid whitesmoke;
 border-radius: 0.3rem;
 text-align: center;
 
}
img{aspect-ratio: 1/1;}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background:linear-gradient(black,black);
  border-bottom: 0.1rem solid black;
  box-shadow: inset 0 0 0.2rem black;
  height: 80px;

  display: flex;
  flex-direction: row;
  align-items: center;
  place-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: aliceblue;
  position: sticky;
  top: 0;
  z-index: 5;
}

.App-header h3,.App-header div{display: flex;flex-direction: row;flex-wrap: wrap;align-items: center;gap: 30px;}
@media screen and (max-width:750px) {
  .App-header{flex-wrap: wrap;height: auto;}
}
@media screen and (max-width:900px) {
  .App-header {font-size: 0.8em;}
  body{font-size: 0.8em;}
}
/*container*/
.container{
  display: block;
  height: auto;
  width: auto;
  background: black;
  color: antiquewhite;
}

label{font-size: 0.5em;}

/*flex-container-row*/
.flex-container-row{
  display: flex;
  flex-direction: row;
  place-content: space-around;
  gap: 10px;
  width: 100%;
  align-items: center;
  margin-top: 10px;
  flex-wrap: wrap;

}
header .flex-container-row{
  width: 20%;
}

/*grid-container*/
.grid-container{
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
  grid-template-rows: repeat(auto-fit,minmax(100px,1fr));
  padding: 2%;
  place-content: space-between;
  align-items: stretch;
  gap: 30px;

  @media screen and (max-width:700px) {
    grid-template-columns: repeat(auto-fit,minmax(150px,1fr));
    
  }
}

/*popMovie*/
.popMovie,.randomMovie{
  display: flex;
  flex-direction: column;
  align-items: center;
  place-content: start;
  position: relative;
transition: 0.3s all;
overflow: hidden;
border: 3px solid rgb(113, 108, 108);
background:rgb(231, 214, 214);
border-radius:0.3rem;
cursor: pointer;
background: black;
color: whitesmoke;
}
.popMovie .poster,.randomMovie .poster{
  width: 100%;
}
.popMovie:hover ,.randomMovie:hover{
transform: scale(1.1,1.1);
z-index: 1;


 
}
.popMovie p,.randomMovie p{display: flex;align-items: center;width: 95%;
  place-content: space;
  padding: 2%;
}
.movieCredits img{
filter: drop-shadow(0 0 0.3rem red);
}
.popMovie:hover.popMovie .description, .randomMovie:hover.randomMovie .description{
 background:#000001f1;
   width: 100%;
  padding:0.3rem;
  border-radius: 0.5rem;
  color: aliceblue;
  box-shadow: 0 0 3rem 0.1rem black;
  
}

.popMovie .description,.randomMovie .description{
  position: absolute;
display: flex;
flex-direction: column;
place-content: center;
align-items: center;
transition: 0.3s ease-in all;
}

.poster{
  display: flex;
  place-content: center;
  align-items: center;

}

/*movieCredits*/
.movieCredits{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  place-content: center;
}
.movieCredits p {width: 70%;
color:white;
padding: 1%;
font-weight: bold;}

/*expanded*/
.expanded{
position: absolute;
background:radial-gradient(circle,rgb(3, 1, 0),black);
z-index: 4;
height: 100%;
width: 100%;
display:grid;
left: 0;
place-content: center;
align-items: center;
transition: 0.3s all linear;
color: aliceblue;
grid-template-columns: repeat(auto-fit,minmax(200px,1fr));

}


.expanded .poster{
  border-radius: 0.5rem;

  height: 100%;
  width: 100%;
  @media screen and (max-width:750px) {
  height: auto;
  width: auto;
  }
}

.isPopular{
position: relative;

border-radius: 0.3rem;
}
.isPopular p{  background: linear-gradient(black,darkblue);
  border: 0.1rem solid aliceblue;
  position: relative;
animation: showcasing 1s linear infinite;}

@keyframes showcasing {
  50%{box-shadow: 0 0 1rem 0.1rem dodgerblue}
}

.isOrdinary p{
  border-radius: 0.3rem;

}
.isGud{
  color: aqua;
}

button{
  background: linear-gradient(dodgerblue,darkblue);
  border: 0.1rem solid dodgerblue;
  box-shadow: 0 0 1rem 0.2rem blue;
padding: 0.5rem;
color:aliceblue;
border-radius: 0.3rem;
cursor: pointer;
font-weight: bold;
transition: 0.3s all linear;
}
button:hover{
  background: black;
  box-shadow: 0 0 1rem 0.3rem black;
 border: 0.1rem solid black;
}
input{
  border-radius: 0.3rem;
  padding: 0.3rem;
  width: 200px;
}
/*loading*/
.loading{height: 100vh;width: 100vw;
position: absolute;
background:rgb(0, 0, 0);
color: aliceblue;
display: flex;
flex-direction: column;
place-content: center;
align-items: center;
z-index:3;}
.loadAni{
  height: 300px;
  width: 300px;
  position: absolute;
  border: 0.2rem transparent ;
  border-bottom: 0.2rem solid dodgerblue ;
  border-radius: 50%;
  animation: loading 1s linear infinite;
}
@keyframes loading {
  to{rotate: 360deg;}
}